<template>
  <div class="page-layout">
    <div class="page-toolbar">
      <toolbar
        @refreshInfo="FETCH_LIST()"
        @newBtnFn="TOGGLE_POPUP()"
        @isSearch="SEARCH_GET()"
        :isNewBtn="false"
        :isBackPath="true"
        :isRefresh="true"
        :isSearchBoxList="false"
        :isSearchDropdown="false"
        :options="pipelineList"
        isBack_specificPath="/"
        newBtnLabel="New Line"
        style="grid-column: span 2"
      />
    </div>

    <div class="page-content">
      <div class="custom-table-header">
        <div class="left">
          <label>Pipeline List</label>
        </div>
        <div class="right table-toolbar-set">
          <v-ons-toolbar-button class="table-toolbar-btn" v-on:click="TOGGLE_POPUPADD()">
            <i class="las la-plus"></i>
            <span>Add New Line</span>
          </v-ons-toolbar-button>
        </div>
      </div>
      <div class="searchbar-box" style="margin-top: 10px">
        <input
          type="text"
          name="search"
          size="50"
          v-model="search_key"
          placeholder="Search Line No."
          class="query"
        />
        <span class="icon">
          <i class="la la-search"></i>
        </span>
        <span class="close" v-if="search_key" v-on:click="SEARCH_CLEAR()">
          <i class="la la-close"></i>
        </span>
      </div>
      <div class="tank-list-list">
        <div class="table-header">
          <div class="item-img"></div>
          <div class="label">
            <label>Line No.</label>
          </div>
          <div class="label">
            <label>Pipe Name.</label>
          </div>
          <div class="label">
            <label>From</label>
          </div>
          <div class="label">
            <label>To</label>
          </div>
          <div class="label">
            <label>Site</label>
          </div>
          <div class="label">
            <label>Inservice Date</label>
          </div>
          <div class="icon"></div>
        </div>
        <div v-if="!search_key">
          <div
            class="item"
            v-for="item in pipelineList"
            :key="item.id"
            v-on:click="VIEW_INFO(item)"
          >
            <div class="item-img">
              <i class="las la-hockey-puck"></i>
            </div>
            <div class="label">
              <label>{{ item.pipeline_no }}</label>
            </div>
            <div class="label">
              <label>{{ item.pipeline_name }}</label>
            </div>
            <div class="label">
              <label>{{ item.pipeline_from }}</label>
            </div>
            <div class="label">
              <label>{{ item.pipeline_to }}</label>
            </div>
            <div class="label">
              <label>{{ item.site_name }}</label>
            </div>
            <div class="label">
              <label>{{ format_inservice_date(item)}}</label>
            </div>
            <div class="icon">
              <i class="las la-angle-right"></i>
            </div>
          </div>
        </div>
        <div v-else>
          <div
            class="item"
            v-for="item in pipelineListFiltered"
            :key="item.id"
            v-on:click="VIEW_INFO(item)"
          >
            <div class="item-img">
              <i class="las la-hockey-puck"></i>
            </div>
            <div class="label">
              <label>{{ item.line_no }}</label>
            </div>
            <div class="label">
              <label>{{ item.line_from }}</label>
            </div>
            <div class="label">
              <label>{{ item.line_to }}</label>
            </div>
            <div class="label">
              <label>{{ item.id_site }}</label>
            </div>
            <div class="label">
              <label>{{ format_inservice_date(item) }}</label>
            </div>
            <div class="icon">
              <i class="las la-angle-right"></i>
            </div>
          </div>
        </div>
      </div>
    </div>
    <contentLoading text="Loading, please wait..." v-if="isLoading == true" color="#fc9b21" />
    <popupAdd v-if="isAdd == true" @closePopup="TOGGLE_POPUPADD()" />
  </div>
</template> 

<script>
//Structures
import contentLoading from "@/components/app-structures/app-content-loading.vue";
import toolbar from "@/components/app-structures/app-navbar-toolbar-flowline.vue";
import popupAdd from "@/views/Applications/FlowLine/info-add.vue";
import { errorMonitor } from "events";
import moment from "moment";

//API
import axios from "/axios.js";

export default {
  name: "PipeLine",
  components: {
    toolbar,
    contentLoading,
    popupAdd
  },
  created() {
    this.$store.commit("UPDATE_CURRENT_INAPP", {
      name: "PipeLine Management",
      icon: "/img/icon_menu/equipment/pipeline.png"
    });
    this.FETCH_LIST();
  },
  data() {
    return {
      isAdd: false,
      isEdit: false,
      isLoading: false,
      editInfo: "",

      pipelineList: [],
      pipelineListFiltered: [],
      dataGridAttributes: {
        class: "data-grid-style"
      },
      search_key: null
    };
  },
  computed: {},
  watch: {
    // whenever search_key change, this function will run
    search_key() {
      this.SEARCH_GET(this.search_key);
    }
  },
  methods: {
    format_inservice_date(item) {
      if (item.inservice_date) {
        return moment(item.inservice_date).format("LL");
      }
      return "N/A";
    },
    VIEW_INFO(item) {
      if (item.id != null) {
        this.$router.push("/pipeline/tag/" + item.id + "/info");
      }
    },
    TOGGLE_POPUPADD() {
      this.isAdd = !this.isAdd;
    },
    FETCH_LIST() {
      this.isLoading = true;
      axios({
        method: "get",
        url: "PipelineInfo",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: null
      })
        .then(res => {
          // console.log(res);
          if (res.status == 200 && res.data) {
            this.pipelineList = res.data;
          }
        })
        .catch(error => {
          console.log(error);
          this.$ons.notification.alert(
            error.code +
              " " +
              error.response.status +
              " " +
              errorMonitor.message
          );
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    SEARCH_CLEAR() {
      this.search_key = null;
    },
    SEARCH_GET(searchValue) {
      let pipelineListFiltered = this.pipelineList;

      if (searchValue != "" && searchValue) {
        pipelineListFiltered = pipelineListFiltered.filter(item => {
          return item.line_no.toUpperCase().includes(searchValue.toUpperCase());
        });
      }
      this.pipelineListFiltered = pipelineListFiltered;
    }
  }
};
</script>

